"use client";

import { Card, CardContent, CardHeader } from "@/components/ui/card";
import { ButtonCustom } from "@/components/ui/button-custom";
import type { Bet, CreateBetForm } from "@/types/bet";

interface QuickBetsMenuProps {
  quickBets: Bet[];
  onSelectQuickBet: (bet: CreateBetForm) => void;
}

export default function QuickBetsMenu({ quickBets, onSelectQuickBet }: QuickBetsMenuProps) {
  const handleSelectQuickBet = (bet: Bet) => {
    // Convert Bet to CreateBetForm format
    const quickBetForm: CreateBetForm = {
      name: bet.name,
      type: bet.type,
      options: bet.options.map(opt => ({ name: opt.name })),
      startDate: new Date(),
      endDate: new Date(),
      isQuickBet: true,
    };
    onSelectQuickBet(quickBetForm);
  };

  if (quickBets.length === 0) {
    return (
      <Card className="bg-gray-900 border-gray-800">
        <CardContent className="p-6">
          <p className="text-gray-400 text-center">
            No hay apuestas rápidas guardadas. Marca la casilla "Añadir a apuestas rápidas" al crear una apuesta para que aparezca aquí.
          </p>
        </CardContent>
      </Card>
    );
  }

  return (
    <Card className="bg-gray-900 border-gray-800">
      <CardHeader>
        <h3 className="text-xl font-bold text-white">Apuestas Rápidas</h3>
      </CardHeader>
      <CardContent>
        <div className="space-y-4">
          {quickBets.map((bet) => (
            <div
              key={bet.id}
              className="flex items-center justify-between p-4 bg-gray-800 rounded-lg hover:bg-gray-700 transition-colors"
            >
              <div>
                <h4 className="text-white font-medium">{bet.name}</h4>
                <p className="text-sm text-gray-400">
                  {bet.options.map(opt => opt.name).join(" vs ")}
                </p>
              </div>
              <ButtonCustom onClick={() => handleSelectQuickBet(bet)}>
                Usar
              </ButtonCustom>
            </div>
          ))}
        </div>
      </CardContent>
    </Card>
  );
}
