"use client";

import { Card, CardContent, CardHeader } from "@/components/ui/card";
import type { Bet } from "@/types/bet";
import { useState } from "react";

// Mock data - In a real app, this would come from an API
const mockFinishedBets: Bet[] = [
  {
    id: "1",
    name: "Copa América 2023",
    type: "binary",
    options: [
      { id: "1", name: "Argentina", points: 8000, participants: 4 },
      { id: "2", name: "Brasil", points: 2000, participants: 1 },
    ],
    startDate: new Date("2023-06-01"),
    endDate: new Date("2023-06-15"),
    status: "finished",
    isQuickBet: false,
    createdAt: new Date("2023-06-01"),
    updatedAt: new Date("2023-06-15"),
  },
  {
    id: "2",
    name: "Mundial Qatar 2022",
    type: "multiple",
    options: [
      { id: "1", name: "Argentina", points: 5000, participants: 5 },
      { id: "2", name: "Francia", points: 3000, participants: 3 },
      { id: "3", name: "Brasil", points: 2000, participants: 2 },
    ],
    startDate: new Date("2022-11-20"),
    endDate: new Date("2022-12-18"),
    status: "finished",
    isQuickBet: true,
    createdAt: new Date("2022-11-20"),
    updatedAt: new Date("2022-12-18"),
  },
];

export default function FinishedBets() {
  const [finishedBets] = useState<Bet[]>(mockFinishedBets);

  const calculateWinnings = (bet: Bet, optionId: string) => {
    const selectedOption = bet.options.find((opt) => opt.id === optionId);
    if (!selectedOption) return 0;

    const totalPoints = bet.options.reduce((sum, opt) => sum + opt.points, 0);
    const losingPoints = totalPoints - selectedOption.points;
    return Math.floor(losingPoints / selectedOption.participants);
  };

  return (
    <div className="space-y-6">
      {finishedBets.map((bet) => (
        <Card key={bet.id} className="bg-gray-800 border-gray-700">
          <CardHeader>
            <div className="flex justify-between items-start">
              <div>
                <h3 className="text-xl font-bold text-white">{bet.name}</h3>
                <p className="text-gray-400">
                  Finalizada el {new Date(bet.endDate).toLocaleDateString()}
                </p>
              </div>
              {bet.isQuickBet && (
                <span className="bg-[#ff6b00] text-white text-xs px-2 py-1 rounded">
                  Apuesta Rápida
                </span>
              )}
            </div>
          </CardHeader>
          <CardContent>
            <div className="space-y-4">
              {bet.options.map((option) => (
                <div
                  key={option.id}
                  className="flex justify-between items-center p-3 bg-gray-900 rounded-lg"
                >
                  <div>
                    <p className="text-white font-medium">{option.name}</p>
                    <p className="text-sm text-gray-400">
                      {option.points} puntos • {option.participants} participantes
                    </p>
                  </div>
                  <div className="text-right">
                    <p className="text-sm text-gray-400">Ganancia por participante</p>
                    <p className="text-white font-bold">
                      {calculateWinnings(bet, option.id)} puntos
                    </p>
                  </div>
                </div>
              ))}
              <div className="mt-4 p-3 bg-gray-900 rounded-lg">
                <p className="text-gray-400 text-sm">
                  Puntos totales apostados:{" "}
                  <span className="text-white font-bold">
                    {bet.options.reduce((sum, opt) => sum + opt.points, 0)}
                  </span>
                </p>
                <p className="text-gray-400 text-sm">
                  Total participantes:{" "}
                  <span className="text-white font-bold">
                    {bet.options.reduce((sum, opt) => sum + opt.participants, 0)}
                  </span>
                </p>
              </div>
            </div>
          </CardContent>
        </Card>
      ))}
    </div>
  );
}
