"use client";

import { useState, useEffect } from "react";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { Card } from "@/components/ui/card";
import CreateBetForm from "@/components/bets/create-bet-form";
import ActiveBets from "@/components/bets/active-bets";
import FinishedBets from "@/components/bets/finished-bets";
import QuickBetsMenu from "@/components/bets/quick-bets-menu";
import Navigation from "@/components/navigation";
import type { Bet, CreateBetForm as BetFormType } from "@/types/bet";

export default function BetsPage() {
  const [activeBets, setActiveBets] = useState<Bet[]>([]);
  const [finishedBets, setFinishedBets] = useState<Bet[]>([]);
  const [quickBets, setQuickBets] = useState<Bet[]>([]);
  const [selectedQuickBet, setSelectedQuickBet] = useState<BetFormType | null>(null);

  // Check for finished bets every minute
  useEffect(() => {
    const checkFinishedBets = () => {
      const now = new Date();
      const newFinishedBets = activeBets.filter(bet => new Date(bet.endDate) <= now);
      const newActiveBets = activeBets.filter(bet => new Date(bet.endDate) > now);

      if (newFinishedBets.length > 0) {
        setActiveBets(newActiveBets);
        setFinishedBets(prev => [...prev, ...newFinishedBets.map(bet => ({ ...bet, status: 'finished' as const }))]);
      }
    };

    const interval = setInterval(checkFinishedBets, 60000); // Check every minute
    return () => clearInterval(interval);
  }, [activeBets]);

  const handleNewBet = (formData: BetFormType) => {
    const newBet: Bet = {
      id: Date.now().toString(),
      ...formData,
      options: formData.options.map((opt, index) => ({
        id: index.toString(),
        name: opt.name,
        points: 0,
        participants: 0,
      })),
      status: 'active',
      createdAt: new Date(),
      updatedAt: new Date(),
    };

    setActiveBets(prev => [...prev, newBet]);

    // If it's a quick bet, add it to quick bets
    if (formData.isQuickBet) {
      setQuickBets(prev => [...prev, newBet]);
    }
  };

  const handleSelectQuickBet = (quickBet: BetFormType) => {
    setSelectedQuickBet(quickBet);
  };

  return (
    <main className="min-h-screen bg-black">
      <Navigation />
      
      <div className="pt-24 pb-16 px-4 sm:px-6 lg:px-8 max-w-7xl mx-auto">
        <div className="text-center mb-12">
          <h1 className="text-4xl font-bold text-white mb-4">Sistema de Apuestas</h1>
          <p className="text-gray-400 text-lg">
            Crea y participa en apuestas con otros usuarios
          </p>
        </div>

        <div className="grid gap-8 md:grid-cols-[2fr,1fr]">
          <Card className="bg-gray-900 border-gray-800">
            <Tabs defaultValue="create" className="w-full">
              <TabsList className="grid w-full grid-cols-3 bg-gray-800 rounded-t-lg">
                <TabsTrigger
                  value="create"
                  className="data-[state=active]:bg-[#ff6b00] data-[state=active]:text-white"
                >
                  Crear Apuesta
                </TabsTrigger>
                <TabsTrigger
                  value="active"
                  className="data-[state=active]:bg-[#ff6b00] data-[state=active]:text-white"
                >
                  Apuestas Activas
                </TabsTrigger>
                <TabsTrigger
                  value="finished"
                  className="data-[state=active]:bg-[#ff6b00] data-[state=active]:text-white"
                >
                  Apuestas Finalizadas
                </TabsTrigger>
              </TabsList>
              <TabsContent value="create" className="p-6">
                <CreateBetForm 
                  onBetCreated={handleNewBet} 
                  initialValues={selectedQuickBet}
                  onAfterSubmit={() => setSelectedQuickBet(null)}
                />
              </TabsContent>
              <TabsContent value="active" className="p-6">
                <ActiveBets bets={activeBets} />
              </TabsContent>
              <TabsContent value="finished" className="p-6">
                <FinishedBets bets={finishedBets} />
              </TabsContent>
            </Tabs>
          </Card>

          <div className="space-y-6">
            <QuickBetsMenu 
              quickBets={quickBets} 
              onSelectQuickBet={handleSelectQuickBet} 
            />
          </div>
        </div>
      </div>
    </main>
  );
}
