"use client";

import { useState, useEffect } from "react";
import type { BetType, CreateBetForm as BetFormType, Bet } from "@/types/bet";
import { ButtonCustom } from "@/components/ui/button-custom";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";

interface CreateBetFormProps {
  onBetCreated: (bet: Bet) => void;
  initialValues?: BetFormType | null;
  onAfterSubmit?: () => void;
}

export default function CreateBetForm({ 
  onBetCreated, 
  initialValues = null,
  onAfterSubmit 
}: CreateBetFormProps) {
  const [form, setForm] = useState<BetFormType>({
    name: "",
    type: "binary",
    options: [{ name: "" }, { name: "" }],
    startDate: new Date(),
    endDate: new Date(),
    isQuickBet: false,
  });

  // Update form when initialValues changes
  useEffect(() => {
    if (initialValues) {
      setForm(initialValues);
    }
  }, [initialValues]);

  const validateForm = (): boolean => {
    if (!form.name.trim()) {
      alert("El nombre de la apuesta es obligatorio.");
      return false;
    }

    const optionNames = form.options.map((opt) => opt.name.trim().toLowerCase());
    const uniqueOptionNames = new Set(optionNames);
    if (uniqueOptionNames.size !== optionNames.length) {
      alert("Las opciones no pueden tener nombres repetidos.");
      return false;
    }

    const now = new Date();
    if (form.startDate < now) {
      alert("La fecha de inicio no puede ser anterior a hoy.");
      return false;
    }
    if (form.endDate < now) {
      alert("La fecha de fin no puede ser anterior a hoy.");
      return false;
    }
    if (form.endDate <= form.startDate) {
      alert("La fecha de fin debe ser posterior a la fecha de inicio.");
      return false;
    }

    return true;
  };

  const handleOptionChange = (index: number, value: string) => {
    const newOptions = [...form.options];
    newOptions[index].name = value;
    setForm({ ...form, options: newOptions });
  };

  const addOption = () => {
    setForm({ ...form, options: [...form.options, { name: "" }] });
  };

  const removeOption = (index: number) => {
    if (form.options.length <= 2) return;
    const newOptions = form.options.filter((_, i) => i !== index);
    setForm({ ...form, options: newOptions });
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();

    if (!validateForm()) {
      return;
    }

    // Create new bet with initial values
    const newBet: Bet = {
      id: Date.now().toString(),
      name: form.name,
      type: form.type,
      options: form.options.map((opt, index) => ({
        id: index.toString(),
        name: opt.name,
        points: 0,
        participants: 0,
      })),
      startDate: form.startDate,
      endDate: form.endDate,
      status: "active",
      isQuickBet: form.isQuickBet,
      createdAt: new Date(),
      updatedAt: new Date(),
    };

    // Call the onBetCreated callback with the new bet
    onBetCreated(newBet);

    alert("Apuesta creada correctamente.");

    // Reset form
    setForm({
      name: "",
      type: "binary",
      options: [{ name: "" }, { name: "" }],
      startDate: new Date(),
      endDate: new Date(),
      isQuickBet: false,
    });

    // Call onAfterSubmit if provided
    onAfterSubmit?.();
  };

  return (
    <form onSubmit={handleSubmit} className="space-y-6 text-white">
      <div>
        <Label htmlFor="name">Nombre de la apuesta</Label>
        <Input
          id="name"
          value={form.name}
          onChange={(e) => setForm({ ...form, name: e.target.value })}
          required
          className="bg-gray-800 text-white border-gray-700 focus:border-[#ff6b00] focus:ring-[#ff6b00]"
          placeholder="Ej: Final Mundial 2024"
        />
      </div>

      <div>
        <Label>Tipo de apuesta</Label>
        <select
          value={form.type}
          onChange={(e) =>
            setForm({
              ...form,
              type: e.target.value as BetType,
              options:
                e.target.value === "binary"
                  ? form.options.slice(0, 2)
                  : form.options,
            })
          }
          className="w-full bg-gray-800 text-white border border-gray-700 rounded-md p-2 focus:border-[#ff6b00] focus:ring-[#ff6b00]"
        >
          <option value="binary">Dos opciones</option>
          <option value="multiple">Múltiple elección</option>
        </select>
      </div>

      <div>
        <Label>Opciones</Label>
        <div className="space-y-3">
          {form.options.map((option, index) => (
            <div key={index} className="flex items-center space-x-2">
              <Input
                value={option.name}
                onChange={(e) => handleOptionChange(index, e.target.value)}
                required
                className="bg-gray-800 text-white border-gray-700 focus:border-[#ff6b00] focus:ring-[#ff6b00]"
                placeholder={`Opción ${index + 1}`}
              />
              {form.type === "multiple" && form.options.length > 2 && (
                <ButtonCustom
                  type="button"
                  variant="secondary"
                  onClick={() => removeOption(index)}
                  className="px-3"
                >
                  ✕
                </ButtonCustom>
              )}
            </div>
          ))}
        </div>
        {form.type === "multiple" && (
          <ButtonCustom
            type="button"
            variant="outline"
            onClick={addOption}
            className="mt-3"
          >
            + Añadir opción
          </ButtonCustom>
        )}
      </div>

      <div className="grid grid-cols-2 gap-4">
        <div>
          <Label htmlFor="startDate">Fecha de inicio</Label>
          <Input
            id="startDate"
            type="datetime-local"
            value={form.startDate.toISOString().slice(0, 16)}
            onChange={(e) =>
              setForm({ ...form, startDate: new Date(e.target.value) })
            }
            required
            min={new Date().toISOString().slice(0, 16)}
            className="bg-gray-800 text-white border-gray-700 focus:border-[#ff6b00] focus:ring-[#ff6b00]"
          />
        </div>

        <div>
          <Label htmlFor="endDate">Fecha de fin</Label>
          <Input
            id="endDate"
            type="datetime-local"
            value={form.endDate.toISOString().slice(0, 16)}
            onChange={(e) => setForm({ ...form, endDate: new Date(e.target.value) })}
            required
            min={new Date().toISOString().slice(0, 16)}
            className="bg-gray-800 text-white border-gray-700 focus:border-[#ff6b00] focus:ring-[#ff6b00]"
          />
        </div>
      </div>

      <div className="flex items-center space-x-2">
        <input
          id="quickBet"
          type="checkbox"
          checked={form.isQuickBet}
          onChange={(e) => setForm({ ...form, isQuickBet: e.target.checked })}
          className="w-4 h-4 rounded bg-gray-800 border-gray-700 text-[#ff6b00] focus:ring-[#ff6b00]"
        />
        <Label htmlFor="quickBet">Añadir a apuestas rápidas</Label>
      </div>

      <ButtonCustom type="submit" className="w-full">
        Crear apuesta
      </ButtonCustom>
    </form>
  );
}
